import { SpeedDial, SpeedDialAction, SpeedDialIcon } from "@mui/material";
import BarChartIcon from "@mui/icons-material/BarChart";
import TimelineIcon from "@mui/icons-material/Timeline";
import DonutLargeIcon from "@mui/icons-material/DonutLarge";
import AcUnitIcon from "@mui/icons-material/AcUnit";
import React, { useContext } from "react";
import { GraphType } from "./Home";
import { Bar, Line, Doughnut, Radar } from "react-chartjs-2";
function Menu() {
  const type = useContext(GraphType);
  const actions = [
    { icon: <BarChartIcon />, name: "Bar" },
    { icon: <TimelineIcon />, name: "Line" },
    { icon: <DonutLargeIcon />, name: "Doughnut" },
    { icon: <AcUnitIcon />, name: "Radar" },
  ];

  const handleClick = (idx) => {
    switch (idx) {
      case 0:
        type.setGraphType(Bar);
        break;
      case 1:
        type.setGraphType(Line);
        break;
      case 2:
        type.setGraphType(Doughnut);
        break;
      case 3:
        type.setGraphType(Radar);
        break;
      default:
        type.setGraphType(Bar);
    }
  };
  return (
    <SpeedDial
      ariaLabel="SpeedDial tooltip example"
      sx={{ position: "absolute", bottom: 16, right: 16 }}
      icon={<SpeedDialIcon />}
    >
      {actions.map((action, i) => (
        <SpeedDialAction
          key={action.name}
          icon={action.icon}
          tooltipTitle={action.name}
          tooltipOpen
          onClick={() => handleClick(i)}
        />
      ))}
    </SpeedDial>
  );
}

export default Menu;
