import React, { useContext } from "react";
import { GraphType } from "./Home";
function Graph(props) {
  const MyComponent = useContext(GraphType);
  console.log(MyComponent);
  return (
    <div className="chart-container">
      <MyComponent.graphType
        data={props && props.chartData}
        options={{
          responsive: true,
          plugins: {
            legend: {
              position: "top",
            },
            title: {
              display: true,
              text: "Covid Stats",
            },
          },
          fill: true,
        }}
      />
    </div>
  );
}

export default Graph;
